.chatbot-container {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  .chatbotContainer .inputArea textarea {
    font-size: 20px;  /* adjust to your preference */
    /* other styles as before */
  }

  .userMessage {
    margin-left: auto;
    background-color: #008080;
    color: white;
    padding: 10px;
    border-radius: 10px;
    width: fit-content;
    margin-bottom: 10px;
  }

  .botMessage {
    margin-right: auto;
    background-color: #D3D3D3; /* Light gray, adjust as desired */
    color: black;
    padding: 10px;
    border-radius: 10px;
    width: fit-content;
    margin-bottom: 10px;
  }
  
  .messages {
    flex-grow: 1;
    overflow-y: auto;
  }
  
  .input-area {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-top: auto;
    padding: 10px;
    border-top: 1px solid #ccc;
  }  
  
  .input-field {
    flex-grow: 1;
    margin-right: 10px;
    padding: 5px;
    height: 100px; /* Adjust this value to change the height of the textarea */
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: none; /* This prevents the user from resizing the textarea */
    font-family: Arial, sans-serif;

  }
  
  button {
    padding: 5px 10px;
    border: none;
    background: white;
    color: black;
    cursor: pointer;
    transition: background 0.3s ease, color 0.3s ease;
  }
  
  button:hover {
    background: white;
    color: teal;
  }
  
  .loading {
    display: inline-block;
    position: relative;
    width: 30px;
    height: 30px;
  }
  
  .loading:after {
    content: " ";
    display: block;
    border-radius: 50%;
    width: 0;
    height: 0;
    margin: 8px;
    box-sizing: border-box;
    border: 10px solid #333;
    border-color: #333 transparent #333 transparent;
    animation: loading 1.2s linear infinite;
  }
  
  @keyframes loading {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  

  .api-key-field {
    width: calc(100% - 58px);
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .message-input-area {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding-bottom: 10px;
  }
  